import { List, ListItem, ListItemText } from '@mui/material';
import { getAge, getTempFromSec, getTimeFromMilliseconds } from '../../../common/date';
import { ICompetitionStatistic } from '../../../features/statistic/statistic.interface';

interface IProp {
	statistic: ICompetitionStatistic;
}

export const CompetitionStatistic: React.FC<IProp> = ({ statistic }) => {
	return (
		<List>
			<ListItem>
				<ListItemText primary={`- : ${statistic.stageCount}`} />
			</ListItem>
			<ListItem>
				<ListItemText primary={`- : ${statistic.userCount}`} />
			</ListItem>
			<ListItem>
				<ListItemText
					primary={` ${statistic.sex.male.genderCount}  ${statistic.sex.female.genderCount}`}
				/>
			</ListItem>
			<ListItem>
				<ListItemText
					primary={`  ${getAge(String(statistic.ageCampare.youngest))}  ${getAge(
						String(statistic.ageCampare.oldest),
					)}`}
				/>
			</ListItem>
			<ListItem>
				<ListItemText
					primary={`  ${getTempFromSec(statistic.tempCampare.fast)}  ${getTempFromSec(
						statistic.tempCampare.slow,
					)}`}
				/>
			</ListItem>
			<ListItem>
				<ListItemText
					primary={`  ${getTimeFromMilliseconds(
						statistic.timeCampare.fast,
					)}  ${getTimeFromMilliseconds(statistic.timeCampare.slow)}`}
				/>
			</ListItem>
			<ListItem>
				<ListItemText
					primary={`     : ${statistic.distanceRun} `}
				/>
			</ListItem>
			<ListItem>
				<ListItemText
					primary={` : ${statistic.fastest.surname} ${
						statistic.fastest.name
					} - ${getTimeFromMilliseconds(statistic.fastest.time)} - ${getTempFromSec(
						statistic.fastest.temp,
					)}`}
				/>
			</ListItem>
		</List>
	);
};
